.navbar {
	background-color:antiquewhite;
	height: 80px;
	display: flex;
	align-items: center;
	font-size: 1.2rem;
	position: sticky;
	top: 0;
	z-index: 20;
	
  }
  
  .nav-container {
	display: flex;
	align-items: center;
	width: 100%;
	height: 80px;
	max-width: 1500px;
	position: absolute;
  }
  
  .nav-logo {
	color: red;
	align-items:flex-start;
	margin-left: 20px;
	cursor: pointer;
	text-decoration: none;
	font-size: 2rem;
	flex-grow: 1;
	position: absolute;
	
  }
  
  .nav-menu {
	display: flex;
	list-style: none;
	text-align: center;
	margin-right: 2rem;
	margin-left: 200px;
  }
  
  .nav-links {
	color: black;
	text-decoration: none;
	padding: 0.5rem 1rem;
	height: 100%;
	border-bottom: 3px solid transparent;
  }
  .fa-code {
	margin-left: 1rem;
  }
  
  .nav-item {
	line-height: 40px;
	margin-right: 1rem;
  }
 
  
  .nav-item:after {
	content: "";
	display: block;
	height: 3px;
	width: 0;
	background: transparent;
	transition: width 0.7s ease, background-color 0.5s ease;
  }
  
  .nav-item:hover:after {
	width: 100%;
	background: red;
  }
  
  .nav-item .active {
	color: red;
	border: 1px solid red;
  }
  
  .nav-icon {
	display: none;
  }
  
  @media screen and (max-width: 960px) {
	.nav-menu {
	  display: flex;
	  flex-direction: column;
	  width: 100%;
	  border-top: 1pxsolid #fff;
	  position: absolute;
	  top: 80px;
	  left: -110%;
	  opacity: 1;
	  transition: all 0.5s ease;
	}
  
	.nav-menu.active {
	  background: #1f5156;
	  left: 0px;
	  opacity: 1;
	  transition: all 0.5s ease;
	  z-index: 1;
	}
	.nav-item .active {
	  color: red;
	  border: none;
	}
	.nav-links {
	  padding: 1.5rem;
	  width: 100%;
	  display: table;
	}
  
	.nav-icon {
	  display: block;
	  position: absolute;
	  top: 0;
	  right: 0;
	  transform: translate(-100%, 60%);
	  font-size: 1.8rem;
	  cursor: pointer;
	  color: red;
	}
  }